#main-side-nav{
  border-bottom: 2px solid black;
  
  ul{
    padding-left: 0;
  }
  
  ul li{
    list-style-type: none;
    padding: .2em;
    font-size: .8em;

    a{
      display: inline-block;
      width: calc(100% - 40px);
      height: 1.4em;
      padding-left: 1em;
    }

  }
  
  li:hover {
    background-color: lightgray;
  }
}